import intro from "./Introduction.module.css";
import AnchorLink from "react-anchor-link-smooth-scroll";
// import ParticleBackground from "../ParticlesBackground/ParticlesBackground";
import ParticlesComponent from "../ParticlesBackground/particles";

function Intro(){
    return(
        <div id='home' className={intro.intro}>
            <ParticlesComponent className={intro.particles} />
            <img alt="Shreya" src={process.env.PUBLIC_URL + "/images/profilePic.JPEG"}/>
            <h1><span>I'm Shreya Redkar,</span> A Full-Stack Developer</h1>
            <p>With 3+ years of expertise in creating scalable web applications and backend solutions.</p>
            <div className={intro.action}>
                <div className={intro.connect}><AnchorLink className={intro.anchorLink} offset={50} href="#contact">Connect with me</AnchorLink></div>
                <div className={intro.resume}>Check out resume</div>
            </div>
        </div>
    )
}

export default Intro;